}

.table-menu-content {
  background: var(--button-bg, white);
  border: 1px solid var(--border-color, #ddd);
  border-radius: 6px;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.1);
  min-width: 180px;
  padding: 4px 0;
}

.table-menu-item {
  display: flex;
  align-items: center;
  padding: 8px 12px;
  cursor: pointer;
  font-size: 14px;
  color: var(--text-color, #333);
  background: var(--button-bg, white);
  border: none;
  width: 100%;
  text-align: left;
  gap: 8px;
  transition: background-color 0.15s ease;
}

