  background-color: #eee;
  margin: 4px 0;
}

/* Dropdown Menu Styles */
.dropdown-menu-container {
  position: fixed;
  z-index: 1000;
}

.dropdown-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 999;
}

.dropdown-menu-content {
  background: var(--button-bg, #fff);
  border: 1px solid var(--border-color, #ddd);
  border-radius: 6px;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.1);
  min-width: 180px;
  padding: 4px 0;
  max-height: 300px;
  overflow-y: auto;
  position: relative;
  z-index: 1001;
}

.dropdown-menu-item {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 12px;
  cursor: pointer;
  background: var(--button-bg, #fff);
  border: none;
  color: var(--text-color, #333);
  text-align: left;
  width: 100%;
  font-size: 14px;
  transition: background-color 0.15s ease;
}

.dropdown-menu-item:hover,
.dropdown-menu-item:focus,
